.skills-card {
    height: 75px;
    width: 75px;
    border-radius: 15px;
    /* background: rgba(240, 93, 122, 0.596); */
    background: rgba(177, 177, 177, 0.596);
    -webkit-box-shadow: 0 11px 36px 0 rgba(70, 89, 138, .25);
            box-shadow: 0 11px 36px 0 rgba(70, 89, 138, .25);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin: 10px;
}

.skills-card img {
    width: 38px;
    height: 38px;
}

.skills-card.animate {
    -webkit-animation-name: example;
            animation-name: example;
}

@-webkit-keyframes example {
    from {
      -webkit-transform: translate3d(-100px, 800px, 300px);
              transform: translate3d(-100px, 800px, 300px);
      opacity: 0;
      -webkit-transition: all .5s ease-in-out;
      -o-transition: all .5s ease-in-out;
      transition: all .5s ease-in-out;
    }
    to {
      -webkit-transform: translate3d(0px, 0px, 0px);
              transform: translate3d(0px, 0px, 0px);
      opacity: 1;
    }
  }

@keyframes example {
    from {
      -webkit-transform: translate3d(-100px, 800px, 300px);
              transform: translate3d(-100px, 800px, 300px);
      opacity: 0;
      -webkit-transition: all .5s ease-in-out;
      -o-transition: all .5s ease-in-out;
      transition: all .5s ease-in-out;
    }
    to {
      -webkit-transform: translate3d(0px, 0px, 0px);
              transform: translate3d(0px, 0px, 0px);
      opacity: 1;
    }
  }


